import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { FaTwitter } from "react-icons/all";
import { Link } from "react-router-dom";
import SocialLink from "../components/SocialLink";
import logo from "../assets/img/Yellow_II.jpg";
// import tweeter from "../assets/img/team_member_6.png";

export default class Footer extends Component {
  render() {
    return (
      <>
        <h3 className="col-12 text-center font-weight-bold sub-title">
          CONTACT US
        </h3>
        <section className="container footer">
          <div>
            <Link to="/">
              <img src={logo} alt="logo"></img>
            </Link>
          </div>
          <div>
            <SocialLink size={30} />
          </div>

          <section className="footer-card">
            <div className="footer-card-header">
              {/* <a href="/">
                      <img
                        src={tweeter}
                        alt="project profile"
                        style={{ width: 50 }}
                      ></img>
                    </a> */}
              <div className="footer-card-title">
                <strong></strong>
                <strong></strong>
              </div>
              <a
                className="TG-link"
                href="https://twitter.com/mambapad_ido"
                target="blank"
                color="white"
              >
                <FaTwitter size={30} />
              </a>
            </div>
            <div className="footer-card-footer">
              {/* <strong style={{ textAlign: "left" }}>
                      The first Launchpad platform for both macro-cap &
                      micro-cap promising projects built on Binance Smart Chain.
                      We aimed to present best projects to our investors by
                      fundraising.
                    </strong> */}
            </div>
          </section>
        </section>
        <hr style={{ borderColor: "#f1b90c", marginTop: "10px" }} />
        <div className="footer-contact">
          <p style={{ textAlign: "center", color: "grey" }}>
            Copyright © 2022, MAMBAPAD. All Right Reserved
          </p>
          <span>
            <a href="mailto:support@mambapad.com">support@mambapad.com</a>
          </span>
        </div>
      </>
    );
  }
}
