import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import { Row, Col, Table, Form, ProgressBar } from "react-bootstrap";
import { ethers } from "ethers";
import { toast } from "react-toastify";
import CountUp, { useCountUp } from "react-countup";
import CountUpCard from "../CountUpCard";
import SocialLinkinCard from "../SocialLinkinCard";
import SocialLinkinCard2 from "../SocialLinkinCard2";
import PropTypes from "prop-types";

import Countdown from "react-countdown";
import { usePrivateSaleApi } from "../../hooks/usePrivateSaleApi";
import { useAccount } from "../../hooks/useAccount";
import { useAppLoading } from "../../hooks/useAppLoading";

export default function PrivateCard({ pool }) {
  const {
    joinPool,
    getBalanceofContract,
    getContractMampBalance,
    getStartTime,
    getEndTime,
    getContribution,
    getDepositedBNB,
    getInfo,
  } = usePrivateSaleApi();
  const { account, getBalanceBNB } = useAccount();
  const { dispatchLoading, dispatchNotLoading } = useAppLoading();
  const [bnbPrice, setBnbPrice] = useState();
  const [amount, setAmount] = useState();
  const [contribution, setContribution] = useState();
  // const [amountofBNB, setAmountofBNB] = useState("");
  const [balanceBNB, setBalanceBNB] = useState("");
  const [isOpen, setIsOpen] = useState(false);
  const [startTime, setStartTime] = useState("");
  const [endTime, setEndTime] = useState("");
  const [info, setInfo] = useState("");
  const [contributionMamp, setContributionMamp] = useState("");
  const [balanceofContract, setBalanceofContract] = useState("");
  const [contractMampBalance, setContractMampBalance] = useState("");
  const [depositedBNB, setDepositedBNB] = useState("");
  const [sellingNow, setSellingNow] = useState(false);
  const [finishNow, setFinishNow] = useState(false);
  const [progress, setProgress] = useState(0);
  const decimal = 10 ^ 8;

  const duration = endTime - startTime;
  const startUTCtime = new Date(startTime).toUTCString();
  const endUTCtime = new Date(endTime).toUTCString();
  const timeOut = startTime - Date.now();
  const getLocalTime = (s) => localStorage.getItem(s) * 1.0;

  useEffect(() => {
    const fetchInfo = async () => {
      getBalanceBNB().then(setBalanceBNB);
      // if (account) {
      //   const gc = await getContribution(account);
      //   setContributionMamp(gc.toString());
      // }
      const info = await getInfo();
      if (info) {
        setInfo(info);
        if (info.startingTime)
          setStartTime(info.startingTime.toString() * 1.0 * 1000);
        if (info.endingTime)
          setEndTime(info.endingTime.toString() * 1.0 * 1000);
        if (info.mampAmountofContract)
          setContractMampBalance(
            ethers.utils.formatUnits(info.mampAmountofContract.toString(), "0")
          );
        if (info.bnbAmountDepositedinContract)
          setDepositedBNB(
            ethers.utils.formatEther(info.bnbAmountDepositedinContract)
          );
        setProgress((depositedBNB / 100) * 100);
      }
      // axios
      //   .get(
      //     `https://api.covalenthq.com/v1/pricing/historical_by_addresses_v2/1/USD/0xb8c77482e45f1f44de1745f52c74426c631bdd52/?quote-currency=USD&format=JSON&key=ckey_docs`
      //   )
      //   .then((res) => {
      //     if (res.data && res.data.data.prices[0].prices[0].price) {
      //       console.log(res.data.data.prices[0].prices[0].price);
      //       setBnbPrice(res.data.data.prices[0].prices[0].price);
      //     }
      //   })
      //   .catch((err) => {
      //     console.error("axios fetch error:", err.data);
      //   });
    };
    fetchInfo();
  }, [
    account,
    // getInfo,
    getBalanceBNB,
    // getContribution,
    // depositedBNB,
    // balanceBNB,
    // contractMampBalance
  ]);

  useEffect(() => {
    const fetchBnbPrice = () => {
      axios
        .get(
          `https://api.covalenthq.com/v1/pricing/historical_by_addresses_v2/1/USD/0xb8c77482e45f1f44de1745f52c74426c631bdd52/?quote-currency=USD&format=JSON&key=ckey_docs`
        )
        .then((res) => {
          if (res.data && res.data.data.prices[0].prices[0].price) {
            setBnbPrice(res.data.data.prices[0].prices[0].price);
          }
        })
        .catch((err) => {
          console.error("axios fetch error:", err.data);
        });
    };
    fetchBnbPrice();
  }, [bnbPrice]);

  // useEffect(() => {
  //   console.log(bnbPrice)
  //   console.log(bnbPrice.toString())
  //   updateValue.current(bnbPrice.toString())
  // }, [bnbPrice, updateValue])

  const handleAmount = (e) => {
    if (e.target.value === "") {
      setAmount("");
      setContribution("");
    } else {
      const value = e.target.value * 1.0;
      setAmount(value);
      setContribution((value / pool.ido_price) * 0.94);
    }
  };
  const handleContribution = (e) => {
    if (e.target.value === "") {
      setAmount("");
      setContribution("");
    } else {
      const value = e.target.value * 1.0;
      setContribution(value);
      setAmount((value * pool.ido_price) / 0.94);
    }
  };

  const jointoPool = async () => {
    const amountofBNB = ethers.utils.parseUnits(amount.toString(), "18");
    const miniumAmount = 10000 * 10 ** 8;
    if (!sellingNow) {
      toast("You Should Wait Until Open Pool...");
      return;
    }
    if (miniumAmount > contractMampBalance) {
      toast("Sorry.. Not Enough MAMP IN POOL NOW..");
      return;
    }
    if (amount === 0 || typeof amount === "string") {
      toast("Please Insert Amount of BNB");
      return;
    }
    if (amount > 2 || amount < 0.1) {
      toast("Min Amount: 0.1 BNB. Max Amount: 2 BNB");
      return;
    }
    if (balanceBNB < amount) {
      toast("Sorry. Your Fund is not Enough... Check Wallet.");
      return;
    }
    try {
      const amountToBuy = await joinPool({
        value: amountofBNB,
      });
      console.log("success");
    } catch (err) {
      if (err.data.message) toast(err.data.message);
    }
  };

  setTimeout(() => {
    setIsOpen(true);
  }, timeOut);

  useEffect(() => {
    localStorage.setItem("start_epoch_time", startTime);
    localStorage.setItem("end_epoch_time", endTime);
    if (timeOut > 0) {
      setSellingNow(false);
      setFinishNow(false);
    }
    if (startTime < Date.now() && endTime > Date.now()) {
      setSellingNow(true);
      setFinishNow(false);
    }
    if (endTime < Date.now()) {
      setSellingNow(false);
      setFinishNow(true);
    }
  }, [timeOut]);

  return (
    <>
      <section className="pool-card">
        <div className="pool-card-header">
          <a href="/">
            <img src={pool.picture} alt="pool profile"></img>
          </a>
          <div className="pool-card-title">
            <strong>{pool.name}</strong>
            <SocialLinkinCard />
          </div>
          <p style={{ color: "orange" }}></p>
        </div>
        <SocialLinkinCard2 />
        <div className="pool-card-body">
          <section>
            <h4 style={{ marginBottom: 10, textAlign: "center" }}>
              MAMP AMOUNT:{"  "}
              <span style={{ fontSize: "30px" }}>
                {ethers.utils.formatUnits(contractMampBalance * 1.0, "8")}
              </span>{" "}
              MAMP{" "}
            </h4>
            <h4 style={{ marginBottom: 10, textAlign: "center" }}>
              BNB AMOUNT:{" "}
              <span style={{ fontSize: "30px" }}>{depositedBNB}</span> BNB{" "}
            </h4>
            <h4 style={{ marginBottom: 10, textAlign: "center" }}>
              START: <span style={{ fontSize: "24px" }}>{startUTCtime}</span>
            </h4>
            {!sellingNow && !finishNow && (
              <h4
                style={{
                  textAlign: 'center'
                  // marginBottom: 0,
                  // display: "grid",
                  // gridTemplateColumns: "50% 50%",
                  // alignItems: "center",
                  // gap: 10,
                }}
              >
                <span
                  // style={{ textAlign: "right" }}
                >{`Pool will open after    `}</span>
                <span style={{ fontSize: "40px" }}>
                  <Countdown
                    date={localStorage.getItem("start_epoch_time") * 1.0}
                    fontSize={20}
                  />
                </span>
              </h4>
            )}
            <CountUpCard value={bnbPrice} />
            <Row
              style={{
                margin: "0px 0px 20px 0px",
                gap: 10,
                alignItems: "center",
              }}
            >
              <Col style={{ padding: 0, flex: 1 }}>
                <Table
                  className="allocation-table"
                  bordered
                  hover
                  variant="dark"
                >
                  <thead>
                    <tr>
                      <th colSpan="2">ALLOCATION(BNB)</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>MIN</td>
                      <td>{pool.min_allocation}</td>
                    </tr>
                    <tr>
                      <td>MAX</td>
                      <td>{pool.max_allocation}</td>
                    </tr>
                  </tbody>
                  <thead>
                    <tr>
                      <th colSpan="2">PRICE(MAMP/BNB)</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>IDO</td>
                      <td>{pool.ido_price}</td>
                    </tr>
                    <tr>
                      <td>LISTING</td>
                      <td>{pool.listing_price}</td>
                    </tr>
                  </tbody>
                </Table>
              </Col>
              <Col style={{ padding: 0, flex: 1 }}>
                <h3 style={{ marginBottom: 3, fontSize: 14 }}>AMOUNT(BNB)</h3>
                <Form.Control
                  style={{ marginBottom: "20px" }}
                  required
                  value={amount}
                  type="number"
                  placeholder="BNB"
                  onChange={handleAmount}
                />
                <div className="join-button">
                  <button className="poolcard-button" onClick={jointoPool}>
                    JOIN POOL
                  </button>
                </div>
                <h3 style={{ marginBottom: 3, marginTop: 70, fontSize: 14 }}>
                  AMOUNT(MAMP)
                </h3>
                <Form.Control
                  type="number"
                  value={contribution}
                  placeholder="MAMP"
                  onChange={handleContribution}
                />
              </Col>
            </Row>
            <ProgressBar
              animated
              now={progress}
              variant="secondary"
              label={`${progress}%`}
              style={{ background: "orange" }}
            />
            <Row style={{ marginTop: "30px" }}>
              {sellingNow && (
                <h3 style={{ marginBottom: 0, fontSize: "24px" }}>
                  WILL LIVE FOR
                </h3>
              )}
              {sellingNow && (
                <div style={{ fontSize: "2em" }}>
                  <Countdown
                    date={localStorage.getItem("end_epoch_time") * 1.0}
                    autoStart={() => {}}
                    onComplete={() => {}}
                    // daysInHours
                  />
                </div>
              )}
              {finishNow && (
                <h3 style={{ marginBottom: 0, fontSize: "44px" }}>CLOSED!!</h3>
              )}
            </Row>
          </section>
        </div>
      </section>
    </>
  );
}

PrivateCard.propTypes = {
  pool: PropTypes.object.isRequired,
};
